import React, { useState, useEffect } from 'react'

import Files from '../files/Files';
import logo from 'assets/images/logo/logo.svg';
import { Comment, Code } from 'components';
import { strings, description } from 'assets/data/data';
import styles from './Container.module.scss';
import { Experience } from './components';

const Container = ({ about, introduction, experience, skills, contact }) => {

    return (
        <div className={`d-flex w-100 h-100 ${styles.wrapper}`}>
            {about ?
                <Files
                    className={styles.introduction}
                    name="About"
                    type="jsx"
                    open={about}
                >

                    <Code tag="h2" className={styles.heading}>
                        About Me
                    </Code>
                    <Code tag="p">
                        {strings.detailedDescription}
                    </Code>
                </Files> :
                introduction ?
                    <Files
                        className={styles.introduction}
                        name="README"
                        type="md"
                        open={introduction}
                    >
                        <img src={logo} alt='logo' className={styles.logo} />
                        <p className={styles.designation}>
                            # {strings.designation}
                        </p> <br />
                        <Comment type="md" className={styles['commented-code']} list={description} />
                    </Files>
                    :
                    experience ?
                        <Files
                            className={styles.introduction}
                            name="Experience"
                            type="scss"
                            open={experience}
                        >
                            <Experience />
                        </Files>
                        :
                        skills ?
                            <Files
                                className={styles.skills}
                                name="Skills"
                                type="jsx"
                                open={skills}
                            >
                                Skills
                            </Files>
                            :
                            contact ?
                                <Files
                                    className={styles.introduction}
                                    name="Contact"
                                    type="jsx"
                                    open={contact}
                                >
                                    Contact
                                </Files>
                                :
                                'Nill :)'
            }


        </div>
    );
};

export default Container
