import React, { useState } from 'react'

import { CssDrawings, ReactWorks } from './components';
import { Comment } from 'components';
import { workDescription } from 'assets/data/data';
import styles from './Works.module.scss';

const Works = () => {
    const [activeTab, setActiveTab] = useState(0);
    const tabData = [
        { label: 'CSS Drawings', content: <CssDrawings /> },
        { label: 'React JS', content: <ReactWorks /> },
        { label: 'UI/UX Designs', content: 'UI/UX Designs' }

    ];
    const handleTabClick = (index) => {
        setActiveTab(index);
    };
    return (
        <div className={`d-flex w-100 h-100  ${styles.wrapper}`}>
        <div className={`d-flex align-items-center ${styles.column}`}>
            <div className={`${styles['exp-wrapper']} ${styles['exp-heading']}`}>
                <h2>
                    Works    
                </h2>
                <Comment
                    className={styles['commented-code']}
                    type="js"
                    list={workDescription}
                />
            </div>
        </div>
        <div className={`d-flex flex-column ${styles.column}`}>
        <ul className={`d-flex ${styles.tabs}`}>
                {tabData.map((tab, index) => (
                    <li
                        key={index}
                        className={`${index === activeTab ? styles.active : ''} ${tab.hideInMobile && styles['hide-in-mobile']} ${styles['tab-item']}`}
                        onClick={() => handleTabClick(index)}
                    >

                        {tab.icon}
                        {tab.label}
                    </li>
                ))}
            </ul>
            <div className={`d-flex h-100 ${styles.container}`}>
                {tabData[activeTab].content}
            </div>

        </div>
    </div>
       
    );
};

export default Works
