import React, { useEffect } from 'react'

import image from 'assets/images/photos/kiranDevadas4.JPG';
import { Button } from 'components';
import { GmailIcon, HireIcon, PriceTagIcon, ResumeIcon, StarIcon, WhatsAppIcon } from 'assets/icons';
import { strings, testimonials, description, experience } from 'assets/data/data';
import styles from './Home.module.scss'
import Location from 'assets/icons/location/Location';

const Home = ({ className }) => {
    const currentDate = new Date();
    const todayDate = new Date();
    currentDate.setMonth(currentDate.getMonth() + 2);
    if (currentDate.getDay() === 6) {
        currentDate.setDate(currentDate.getDate() + 2);
    } else if (currentDate.getDay() === 0) {
        currentDate.setDate(currentDate.getDate() + 1);
    }
    const todayDay = todayDate.getDate();
    const todayMonth = todayDate.toLocaleString('default', { month: 'short' });
    const todayDayOfWeek = todayDate.toLocaleString('default', { weekday: 'long' });
    const todayFormattedDate = `${todayDay}${getDaySuffix(todayDay)} ${todayMonth}, ${todayDayOfWeek}`;
    const day = currentDate.getDate();
    const month = currentDate.toLocaleString('default', { month: 'short' });
    const dayOfWeek = currentDate.toLocaleString('default', { weekday: 'long' });
    const formattedDate = `${day}${getDaySuffix(day)} ${month}, ${dayOfWeek}`;

    return (
        <div className={`${styles.wrapper} ${className}`}>
            <div className={`d-flex ${styles.container}`}>
                <div className={styles['image-wrapper']}>
                    <div className={styles.image}>
                        <img src={image} alt='Kiran Devadas' />
                    </div>
                    <div className={`d-flex ${styles['button-wrapper']}`}>
                        <Button className={styles['resume-button']} label="Download Resume" leftIcon={<ResumeIcon />} />
                        <Button className={styles['hire-button']} label="Hire Me" leftIcon={<HireIcon />} />
                    </div>
                </div>
                <div className={styles['details-wrapper']}>
                    <ul className={`d-flex list-style-none ${styles.breadcrumb}`}>
                        <li>
                            Portfolio
                        </li>
                        <li>
                            Themes
                        </li>
                        <li>
                            Flipkart
                        </li>
                    </ul>
                    <h2 className={styles.title}>
                        {strings.firstName + " " + strings.lastName}
                    </h2>
                    <div className={`d-flex align-items-center ${styles['testimonials-wrapper']}`}>
                        <div className={`d-flex align-items-center ${styles.rating}`}>
                            4.2
                            <StarIcon />
                        </div>
                        <p>
                            {testimonials.length} Testimonials
                        </p>
                    </div>
                    <p className={styles.designation}>
                        {strings.designation}
                    </p>
                    <ul className={`list-style-none ${styles.list}`}>
                        {description.map((descriptionItem) => (
                            <li className='d-flex align-items-center' key={descriptionItem.id}>
                                <PriceTagIcon />
                                {descriptionItem.label}
                            </li>
                        ))}
                    </ul>
                    <div className={`${styles['key-points']}`}>
                        <div className='d-flex'>
                            <p className={styles.label}>
                                Location
                            </p>
                            <div className={styles.information}>
                                <div className={`d-flex align-items-center ${styles.pin}`}>
                                    <Location />
                                    {strings.address + " " + strings.pinCode}
                                </div>
                                <h5 className={styles['join-date']}>
                                    Can join by {formattedDate}. | <span>60 Days</span>
                                    <small>
                                        If hired before {todayFormattedDate}.
                                    </small>
                                </h5>
                            </div>
                        </div>
                        <div className='d-flex'>
                            <p className={styles.label}>
                                Email
                            </p>
                            <div className={`d-flex align-items-center ${styles.information} ${styles.email}`}>
                                <GmailIcon /> {strings.email}
                            </div>
                        </div>
                        <div className='d-flex'>
                            <p className={styles.label}>
                                Mobile
                            </p>
                            <div className={`d-flex align-items-center ${styles.information} ${styles.number}`}>
                                <WhatsAppIcon /> {strings.phone}
                            </div>
                        </div>
                    </div>
                    <div className={styles.description}>
                        <h5>
                            About
                        </h5>
                        <p>
                            {strings.detailedDescription}
                        </p>
                    </div>
                    <div className={styles.review}>
                        <h5>
                            Testimonials
                        </h5>
                        {testimonials.map((testimonialsItem) => (
                            <div className={styles['testmonial-item']} key={testimonialsItem.id}>
                                <div className={`d-flex align-items-center ${styles.rating}`}>
                                    4.2
                                    <StarIcon />
                                </div>
                                <div className={styles.message}>
                                    {testimonialsItem.message}
                                </div>
                                <div>
                                <div className={`d-flex align-items-center ${styles['user-info']}`}>
                                    <img className={styles.dp} src={testimonialsItem.image} alt='Image of ' />
                                    <h4>
                                        {testimonialsItem.name}, {testimonialsItem.designation}, {testimonialsItem.organisation}
                                    </h4>
                                </div>
                                </div>
                            </div>
                        ))}
                    </div>
                </div>
            </div>
        </div>
    );
};

function getDaySuffix(day) {
    if (day >= 11 && day <= 13) {
        return 'th';
    }
    switch (day % 10) {
        case 1:
            return 'st';
        case 2:
            return 'nd';
        case 3:
            return 'rd';
        default:
            return 'th';
    }
}

export default Home
