import React, { useState, useEffect } from 'react';

import { strings } from 'assets/data/data';

import SocialMedia from '../social-media/SocialMedia';
import roundedDraw from 'assets/images/icons/circle.png';
import { ProgrammingIcon } from 'assets/icons';
import styles from './Header.module.scss';

const Header = ({ themeSwitch, themeMsg }) => {
    const [showTooltip, setShowTooltip] = useState(false);

    useEffect(() => {
        // Check local storage to see if the user has clicked on themeMsg before
        const hasThemeMsgClicked = localStorage.getItem('themeMsgClicked');

        if (!hasThemeMsgClicked) {
            const tooltipTimeout = setTimeout(() => {
                setShowTooltip(true);
            }, 2500);

            document.addEventListener('click', handlePageClick);

            return () => {
                clearTimeout(tooltipTimeout);
                document.removeEventListener('click', handlePageClick);
            };
        }
    }, []);

    const handlePageClick = () => {
        setShowTooltip(false);
    };

    useEffect(() => {
        const hideTooltipTimeout = setTimeout(() => {
            setShowTooltip(false);
        }, 40000);

        return () => {
            clearTimeout(hideTooltipTimeout);
        };
    }, [showTooltip]);

    const handleThemeMsgClick = () => {
        // Mark themeMsg as clicked
        localStorage.setItem('themeMsgClicked', 'true');
        setShowTooltip(false); // Hide the tooltip
    };

    return (
        <div className={styles.wrapper}>
            <div className={`d-flex align-items-center ${styles.logo}`}>
                <ProgrammingIcon />
                {/* <p>{strings.firstName + '_' + strings.lastName}</p> */}
                <p>{strings.domain.split('.').join('(') + ')'}</p>
            </div>
            <div className={styles.socialmedia}>
                <SocialMedia />
            </div>
            <div className={`${styles.tooltip} ${showTooltip ? styles.show : ''}`}>
                {strings.themeTooltip}
            </div>
            <button
                className={styles['theme-button']}
                onClick={handleThemeMsgClick}
            >
                {themeSwitch}
            </button>

        </div>
    );
};

export default Header;
