import React, { useState, useEffect } from 'react';
import airbnb from 'assets/images/react-projects/airbnb-replica.jpg';
import { CodeIcon, NewWindowIcon, PreviewIcon } from 'assets/icons';
import { Button, Skeleton } from 'components';
import { cssWorksData } from 'assets/data/data';
import styles from './CssDrawings.module.scss';

const CssDrawings = () => {
  const [imageLoaded, setImageLoaded] = useState(false);
  const [showSkeleton, setShowSkeleton] = useState(true);

  useEffect(() => {
    const delay = 2000;
    const timer = setTimeout(() => {
      setShowSkeleton(false);
    }, delay);

    return () => clearTimeout(timer);
  }, []);

  return (
    <div className={`d-flex w-100 ${styles.wrapper}`}>
      {cssWorksData.map((works) => (
        <div className={styles['frame-wrapper']}>
          <div className={styles.frame}>
            <img
              src={works.imageSrc}
              alt="image of sky"
              loading="lazy"
              onLoad={() => {
                setImageLoaded(true);
                console.log('Image loaded successfully');
              }}
              onError={(e) => {
                console.error('Image failed to load:', e);
              }}
            />
          </div>
          <h2>{works.title}</h2>
          <p>
            {works.description}
          </p>
          <div className={`d-flex align-items-center ${styles['actions-wrapper']}`}>
            <div className={`d-flex align-items-center ${styles.actions}`}>
              <PreviewIcon />
              <Button
                target="_blank"
                type="anchor"
                label="Preview"
                href={works.previewLink}
              />
              <NewWindowIcon />
            </div>
            <div className={`d-flex align-items-center ${styles.actions}`}>
              <CodeIcon />
              <Button
                target="_blank"
                type="anchor"
                label="Code"
                href={works.codeLink}
              />
              <NewWindowIcon />
            </div>
          </div>
        </div>
      ))}
    </div>
  );
};

export default CssDrawings;
