import React, { useState, useRef } from 'react';
import styles from './VSCode.module.scss';
import { SideBar, FolderExplorer, Container, Footer } from './components';
import { strings } from 'assets/data/data';

const VSCode = () => {
    const [activeTab, setActiveTab] = useState('introduction');
    const textareaRef = useRef(null);
    const alertRef = useRef(null);

    const handleTabChange = (tab) => {
        setActiveTab(tab);
    };

    const tabKeywordMap = {
        'about': 'about',
        'readme': 'readme',
        'experience': 'experience',
        'skills': 'skills',
        'contact': 'contact',
    };

    const handleTextareaKeyPress = (event) => {
        if (event.key === 'Enter') {
            const inputText = event.target.value.trim().toLowerCase();
            if (tabKeywordMap.hasOwnProperty(inputText)) {
                handleTabChange(tabKeywordMap[inputText]);
                event.target.value = '';
                displayMessageInAlert(`You've successfully navigated to the ${tabKeywordMap[inputText]}  page!  😍 `, 'success');
            } else {
                displayMessageInAlert(
                    'Invalid request. Please enter a valid keyword 😒 <div>Possible commands; <span>about, readme, experience, skills, contact</span> </div>',
                    'error'
                );
                event.target.value = '';
            }
        }
    };

    const displayMessageInAlert = (message, messageType) => {
        const alertDiv = alertRef.current;
        alertDiv.innerHTML = message;
        alertDiv.className = `${styles.alert} ${styles[messageType]}`;
    };

    const clearAlert = () => {
        const alertDiv = alertRef.current;
        alertDiv.innerHTML = '';
    };

    return (
        <div className={`d-flex w-100 flex-column ${styles.container}`}>
            <div className='d-flex h-100'>
                <SideBar />
                <FolderExplorer
                    projectName={strings.projectName}
                    activeTab={activeTab} // Pass the activeTab state
                    handleAbout={() => handleTabChange('about')}
                    handleIntroduction={() => handleTabChange('introduction')}
                    handleExperience={() => handleTabChange('experience')}
                    handleSkills={() => handleTabChange('skills')}
                    handleContact={() => handleTabChange('contact')}
                />
                <div className='w-100'>
                    <Container
                        about={activeTab === 'about'}
                        introduction={activeTab === 'introduction'}
                        experience={activeTab === 'experience'}
                        skills={activeTab === 'skills'}
                        contact={activeTab === 'contact'}
                    />
                    <div className={styles.terminal}>
                        <h3>TERMINAL</h3>
                        <div className='d-flex'>
                            <p>{strings.projectName} %</p>
                            <input
                                ref={textareaRef}
                                autoFocus
                                onKeyPress={handleTextareaKeyPress}
                                type='text'
                                placeholder='Ex: about'
                            />
                        </div>
                        <div ref={alertRef}></div>
                    </div>
                </div>
            </div>
            <Footer />
        </div>
    );
};

export default VSCode;
