import React from 'react'

import { CssIcon, DocumentIcon, FolderIcon, JSIcon, MdIcon, ReactJSIcon, ScssIcon } from 'assets/icons';
import styles from './FileName.module.scss'

const FileName = ({type, name, children}) => {
    return (
        <div className={styles.wrapper}>
            <div className={`d-flex align-items-center ${styles['file-name']}`}>
                {type == "js" ? <JSIcon/> : type == "folder" ? <FolderIcon/> : type == "jsx" ? <ReactJSIcon/> : type == "scss" ? <ScssIcon/> : type == "css" ? <CssIcon/> : type == "md" ? <MdIcon/> : <DocumentIcon/> }
                
                {name}{type == "js" ? '.js' : type == "jsx" ? '.jsx' : type == "css" ? '.css' : type == "scss" ? '.scss' : type == "md" ? '.md' : ''}
            </div>
        </div>
    );
};

export default FileName
