import React from 'react';
import useFetch from 'hooks/useFetch';
import { Comment } from 'components';
import NewWindow from 'assets/icons/new-window';
import { expDescription } from 'assets/data/data';
import styles from './Experience.module.scss';


const Experience = () => {
    const { loading, error, data } = useFetch('http://localhost:1337/api/experiences');

    console.log("Loading:", loading);
    console.log("Error:", error);
    console.log("Data:", data);

    if (loading) return <p>Loading...</p>;
    if (error) return <p>Error: {error.message}</p>;
    if (!data || !Array.isArray(data.data)) {
        return <p>No data available</p>;
    }

    function getYearFromDate(dateString) {
        if (!dateString) {
            return 'Present';
        }
        const date = new Date(dateString);
        return date.getFullYear();
    }

    // Sort the data based on the end date, with the latest experience first
    const sortedData = data.data.sort((a, b) => {
        const endDateA = new Date(a.attributes.endDate || '9999-12-31');
        const endDateB = new Date(b.attributes.endDate || '9999-12-31');
        return endDateB - endDateA;
    });

    return (
        <div className={`d-flex w-100 h-100 ${styles.wrapper}`}>
            <div className={`d-flex align-items-center w-100 h-100 ${styles.column}`}>
                <div className={`${styles['exp-heading']} ${styles['exp-wrapper']}`}>
                    <h2>Experience</h2>
                    <Comment
                        className={styles['commented-code']}
                        type="js"
                        list={expDescription}
                    />
                </div>
            </div>
            <div className={`d-flex ${styles.column}`}>
                <div className={styles['exp-wrapper']}>
                    {sortedData.map(experience => (
                        <div className={styles['exp-items']} key={experience.id}>
                            <div className={`d-flex ${styles['exp-inner-wrapper']}`}>
                                <div className={styles.year}>
                                    {getYearFromDate(experience.attributes.startDate)} - {experience.attributes.endDate ? getYearFromDate(experience.attributes.endDate) : 'Present'}
                                </div>
                                <div className={styles['exp-details']}>
                                    <div className='d-flex align-items-center'>
                                        <h4>
                                            {experience.attributes.designation}
                                        </h4>
                                        <span className={styles.dot} />
                                        <a target='_blank' href={experience.attributes.website} className={`d-flex align-items-center ${styles.organisation}`}>
                                            {experience.attributes.organisation}
                                            <NewWindow />
                                        </a>
                                    </div>
                                    <p>{experience.attributes.description}</p>
                                    <ul className={`d-flex list-style-none ${styles.tag}`}>
                                        {experience.attributes.skills && experience.attributes.skills.split(',').map((skill, index) => (
                                            <li key={index}>{skill.trim()}</li>
                                        ))}
                                    </ul>

                                </div>
                            </div>
                        </div>
                    ))}
                </div>
            </div>
        </div>
    );
};

export default Experience;
